@import "../../../assets/style/regular";
@import "./AlternativeDataV2/index";
@import "HowItWorks/index";

@mixin who-are-we {
	.who-are-we {
		margin: auto;
		padding: 0 105px;
		max-width: 1440px;
		box-sizing: border-box;
		.top {
			text-align: center;
			margin-bottom: 80px;
			p {
				margin: 0;
				display: inline-block;
				font-family: "Rubik", sans-serif;
				&:nth-child(1) {
					color: #A7A9C0;
					display: block;
					font-size: 24px;
					font-weight: 500;
					margin-bottom: 16px;
				}
				&:nth-child(2) {
					margin-left: unset !important;
				}
				&:not(:first-child) {
					color: #303037;
					font-size: 48px;
					margin-left: 10px;
					font-weight: bold;
					line-height: 125%;
				}
			}
		}
		.bottom {
			$size: 48px;
			width: 100%;
			display: flex;
			.each-point {
				position: relative;
				width: calc(100% / 3);
				padding: $size + 24px 15px 0 15px;
				p {
					margin: 0;
					color: #303037;
					position: relative;
					&:nth-child(2) {
						font-size: 24px;
						line-height: 160%;
						font-weight: bold;
						font-family: "Rubik", sans-serif;
					}
					&:last-child {
						font-size: 18px;
						margin-top: 16px;
						line-height: 160%;
						font-family: "SF Compact Text Regular", sans-serif;
					}
				}
				img {
					width: $size;
					height: $size;
					top: $size / 2;
					position: absolute;
					transform: translateY(-50%);
				}
				&:first-child {
					padding-left: 0;
				}
				&:last-child {
					padding-right: 0;
				}
			}
		}
	}
	@media only screen and (max-width: $desktop-sm) {
		.who-are-we .top p {
			margin-left: unset !important;
			&:last-child {
				margin-left: 10px !important;
			}
		}
	}
	@media only screen and (max-width: $tablet) {
		.who-are-we {
			padding: 0 16px;
			.top {
				max-width: 560px;
				margin: 0 auto 80px auto;
				p {
					&:first-child {
						font-size: 18px;
					}
					&:not(:first-child) {
						font-size: 28px;
					}
					&:last-child {
						margin-left: 8px !important;
					}
				}
			}
			.bottom {
				$size: 48px;
				margin: auto;
				max-width: 560px;
				flex-direction: column;
				.each-point {
					width: unset;
					max-width: 560px;
					margin-bottom: 40px + 18px;
					padding: 0 0 0 24px + $size !important;
					p {
						&:nth-child(2) {
							font-size: 18px;
						}
					}
					img {
						left: 0;
						top: 14px;
						width: $size;
						height: $size;
					}
					&:last-child {
						margin-bottom: 0;
					}
				}
			}
		}
	}
	@media only screen and (max-width: $mobile) {
		.who-are-we {
			.top p:last-child {
				margin-left: unset !important;
			}
			.bottom .each-point {
				padding-left: 64px !important;
				img {
					$size: 40px;
					top: 14px;
					width: $size;
					height: $size;
				}
			}
		}
	}
}

@mixin what-we-do {
	.what-we-do {
		margin-top: 100px;
		position: relative;
		padding: 140px 0 400px 0;
		img {
			top: 0;
			left: 50%;
			position: absolute;
			transform: translateX(-45.5%);
		}
		.header {
			position: relative;
			p {
				margin: 0;
			}
			.title {
				color: #00D7FE;
				font-size: 24px;
				font-weight: 500;
				text-align: center;
				font-family: "Rubik", sans-serif;
			}
			.subtitle {
				margin-top: 16px;
				margin-bottom: 80px;
				p {
					color: #FFFFFF;
					font-size: 48px;
					font-weight: bold;
					line-height: 125%;
					text-align: center;
					font-family: "Rubik", sans-serif;
				}
			}
		}
		.content {
			width: 95%;
			margin: auto;
			display: flex;
			position: relative;
			flex-direction: row;
			justify-content: center;
			.content-card {
				width: 100%;
				height: 325px;
				max-width: 600px;
				align-self: center;
				border-radius: 8px;
				padding: 40px 0 47px 0;
				box-sizing: border-box;
				background-color: #ffffff;
				box-shadow: 0 40px 80px rgba(27, 30, 78, 0.08);
				&:first-child {
					margin-right: 15px;
				}
				&:last-child {
					margin-left: 15px;
				}
				p {
					margin: 0;
				}
				.header {
					width: 100%;
					margin: auto;
					color: #303037;
					font-size: 36px;
					max-width: 300px;
					font-weight: bold;
					line-height: 125%;
					text-align: center;
					font-family: "Rubik", sans-serif;
				}
				.content {
					width: 100%;
					color: #303037;
					font-size: 18px;
					max-width: 380px;
					margin-top: 40px;
					line-height: 160%;
					margin-left: auto;
					margin-right: auto;
					text-align: center;
					font-family: "SF Compact Text Regular", sans-serif;
				}
			}
		}
	}
	@media only screen and (max-width: $tablet) {
		.what-we-do {
			margin-top: 130px;
			padding-bottom: 250px;
			img {
				width: 2350px;
			}
			.header .subtitle p {
				margin: auto;
				max-width: 558px;
			}
			.content {
				flex-direction: column;
				.content-card {
					height: unset;
					max-width: 600px;
					max-height: 275px;
					margin-bottom: 24px;
					margin-left: 0 !important;
					margin-right: 0 !important;
					padding: 16px 16px 42px 16px;
					&:last-child {
						margin-bottom: 0;
					}
					.header {
						margin: auto;
						font-size: 22px;
						max-width: 180px;
					}
					.content {
						font-size: 16px;
						max-width: none;
						margin-top: 20px;
					}
				}
			}
		}
	}
	@media only screen and (max-width: $mobile) {
		.what-we-do {
			padding-top: 80px;
			img {
				width: auto;
				transform: translateX(-51.5%);
			}
			.header {
				.title {
					font-size: 18px;
				}
				.subtitle p {
					margin: auto;
					font-size: 28px;
					max-width: 350px;
				}
			}
			.content {
				.content-card {
					width: 90%;
					padding: 24px;
					max-width: 344px;
				}
			}
		}
	}
}

@mixin machine-learning {
	.machine-learning {
		margin: auto;
		display: flex;
		max-width: 1220px;
		justify-content: space-between;
		.left {
			display: flex;
			max-width: 500px;
			margin-right: 30px;
			flex-direction: column;
			justify-content: center;
			.declaration {
				margin: 0;
				&:nth-child(1) {
					color: #A7A9C0;
					font-size: 24px;
					font-weight: 500;
					font-family: "Rubik", sans-serif;
				}
				&:nth-child(2) {
					color: #303037;
					font-size: 48px;
					font-weight: bold;
					line-height: 125%;
					margin: 16px 0 24px 0;
					font-family: "Rubik", sans-serif;
				}
				&:nth-child(3) {
					color: #303037;
					font-size: 20px;
					line-height: 160%;
					font-family: "SF Compact Text Regular", sans-serif;
				}
			}
		}
		.right {
			flex-grow: 1;
			img {
				width: 100%;
				max-width: 705px;
			}
		}
	}
	@media only screen and (max-width: $desktop-sm) {
		.machine-learning {
			flex-direction: column;
			.left {
				margin: auto;
				max-width: 600px;
				.declaration {
					text-align: center;
				}
			}
			.right {
				margin-top: 48px;
				img {
					margin: auto;
					display: block;
					max-width: 600px;
					transform: translateX(48px);
				}
			}
		}
	}
	@media only screen and (max-width: $tablet) {
		.machine-learning .right img {
			transform: none;
		}
	}
	@media only screen and (max-width: $mobile) {
		.machine-learning {
			.left {
				max-width: 340px;
				.declaration {
					&:nth-child(1) {
						font-size: 18px;
					}
					&:nth-child(2) {
						font-size: 28px;
					}
				}
			}
			.right img {
				width: 90%;
				transform: none;
				max-width: 425px;
			}
		}
	}
}

@mixin about-us-container {
	.about-us-container {
		width: 100%;
		padding-top: 50px;
		box-sizing: border-box;
		@include who-are-we;
		@include what-we-do;
		@include alternative-data;
		@include how-it-works;
		@include machine-learning;
	}
	@media only screen and (max-width: $mobile) {
		.about-us-container {
			padding-top: 174px;
		}
	}
}